window.global = window;
var g = {};
window.g = g;


var socket = io();
g.socket = socket;
g.path = __dirname;

g.userId = false;

socket.on('connect', function(){
  var userId = sessionStorage.getItem('userId');
  console.log('iAm', userId);
  socket.emit('iAm', userId);

  socket.on('youAre', function(serveSaysUserId){
    if( userId !== serveSaysUserId ){
      userId = serveSaysUserId;
      sessionStorage.setItem('userId', userId);
      console.log('i am', userId);
    } else {
      console.log('userId confirmed');
    }

    g.userId = userId;
    socket.on('server_says', function(msg){
      console.log('server_says', msg);
    });

    //console.log('testing server communication');
    var number = chance.d20();
    //console.log('number:', number );
    //socket.emit('test', number);

    socket.emit('db_set', {
      userId: g.userId,
      doc: {
        status: 'this is a test',
        number: number
      }
    });




  });
});


window.addEventListener('load', function(){
  //html, body {
  //  overflow: hidden;
  //  width   : 100%;
  //  height  : 100%;
  //  margin  : 0;
  //  padding : 0;
  //}
  //
  //#renderCanvas {
  //  width   : 100%;
  //  height  : 100%;
  //  touch-action: none;
  //}

  var content = SpecDOM('#content');
  content.load({
    tag: 'canvas',
    props:{
      id: 'renderCanvas'
    }
  });



  // get the canvas DOM element
  var canvas = document.getElementById('renderCanvas');



  // load the 3D engine
  var engine = new BABYLON.Engine(canvas, true);

  // createScene function that creates and return the scene
  var createScene = function(){
    // create a basic BJS Scene object
    var scene = new BABYLON.Scene(engine);

    var colors = {
      brown: new BABYLON.Color3(139/255,69/255,19/255),
      grey: new BABYLON.Color3(190/255,190/255,190/255),
      green: new BABYLON.Color3(34/255,139/255,34/255),
      sunset: new BABYLON.Color3(255/255,240/255,200/255)

    };
    var materials = {};

    materials.wood = new BABYLON.StandardMaterial('mymat', scene);
    materials.wood.diffuseColor = colors.brown;

    materials.concrete = new BABYLON.StandardMaterial('mymat', scene);
    materials.concrete.diffuseColor = colors.grey;

    materials.grass = new BABYLON.StandardMaterial('mymat', scene);
    materials.grass.diffuseColor = colors.green;




    // create a FreeCamera, and set its position to (x:0, y:5, z:-10)
    var camera = new BABYLON.FreeCamera('camera1', new BABYLON.Vector3(10, 2, 10), scene);

    camera.checkCollisions = true;
    camera.collisionRadius = new BABYLON.Vector3(0.5, 0.5, 0.5);

    // target the camera to scene origin
    camera.setTarget(BABYLON.Vector3.Zero());

    // attach the camera to the canvas
    camera.attachControl(canvas, false);

    // create a basic light, aiming 0,1,0 - meaning, to the sky
    var sun = new BABYLON.HemisphericLight('light1', new BABYLON.Vector3(1,1,1), scene);
    sun.diffuse = colors.sunset;


    // create a built-in 'ground' shape; its constructor takes the same 5 params as the sphere's one
    var ground = BABYLON.Mesh.CreateGround('ground1', 1000, 1000, 2, scene);
    ground.material = materials.concrete;

    var buildingSize = 10;

    var grass = BABYLON.Mesh.CreateGround('ground1', buildingSize*1.5*4, buildingSize*1.5*4, 2, scene);
    grass.material = materials.grass;
    grass.position.y = 0.1

    // create a built-in 'sphere' shape; its constructor takes 5 params: name, width, depth, subdivisions, scene
    var sphere = BABYLON.Mesh.CreateSphere('sphere1', 16, 3, scene);
    // move the sphere upward 1/2 of its height
    sphere.position.y = 0;

    var centralLight = new BABYLON.SpotLight('light1', new BABYLON.Vector3(0,1,5,0), new BABYLON.Vector3(0,1,0), 30, 0, scene);
    centralLight.diffuse = colors.green;

    var box = BABYLON.Mesh.CreateBox('box', 2, scene, false);
    box.position.y = 4;
    box.rotation.y = 45 * (Math.PI/180);
    box.rotation.x = 45 * (Math.PI/180);
    //box.rotation.z = 45 * (Math.PI/180);





    var building = function(scene, x, z, y){
      var building = BABYLON.Mesh.CreateBox('building_'+x+y+z, buildingSize, scene, false);
      building.material = materials.wood;
      building.position.y = buildingSize/2;
      building.position.x = x;
      building.position.z = z;
    };


    _.times(8, function(i){
      _.times(8, function(j){
        if( i > 1 || j > 1 ){
          building(scene,  buildingSize +  (buildingSize*1.5)*i,  buildingSize +  (buildingSize*1.5)*j);
          building(scene, -buildingSize + -(buildingSize*1.5)*i, -buildingSize + -(buildingSize*1.5)*j);
          building(scene, -buildingSize + -(buildingSize*1.5)*i,  buildingSize +  (buildingSize*1.5)*j);
          building(scene,  buildingSize +  (buildingSize*1.5)*i, -buildingSize + -(buildingSize*1.5)*j);
        }

      });
    });


    // return the created scene
    return scene;
  };

  // call the createScene function
  var scene = createScene();

  var update = function(){
    var box = scene.getMeshByName('box');
    box.rotation.y +=  1 * (Math.PI/180);
    if( box.rotation.y > Math.PI * 2) box.rotation.y -= Math.PI * 2;
    box.rotation.x +=  1 * (Math.PI/180);
    if( box.rotation.x > Math.PI * 2) box.rotation.x -= Math.PI * 2;
    box.rotation.z +=  1 * (Math.PI/180);
    if( box.rotation.z > Math.PI * 2) box.rotation.z -= Math.PI * 2;
  };
  scene.registerBeforeRender(update);

  // run the render loop
  engine.runRenderLoop(function(){
    scene.render();
  });




  // the canvas/window resize event handler
  var resizeWindow = function(){
    engine.resize();
    canvas.setAttribute('height', window.innerHeight);
  };
  window.onresize = resizeWindow;
  resizeWindow();

});



